<template>
 <div class="center">
      <div class="center_breadcrumb">
        <crumb :data="pageTitle"></crumb>
    </div>
    <div class="center_content">
        <div class="center_content_search">
            <div class="center_content_search_left">
                <div class="search-input-suffix gap">
                    <label>问卷类型：</label>
                    <el-select v-model="page.type" placeholder="全部">
                        <el-option
                        v-for="item in typeOptions"
                        :key="item.id"
                        :label="item.classifyName"
                        :value="item.id">
                        </el-option>
                    </el-select>
                </div>
                <div class="search-input-suffix">
                    <label>问卷搜索：</label>
                    <el-input class="search"
                            @keyup.enter.native="search"
                            v-model="page.keyword"
                            placeholder="请输入搜索关键词"
                            clearable
                            >                       
                    </el-input>
                     <div style="margin-left: 20px" @click="search" class="search--btn">搜索</div>
                </div>
            </div>
        </div>
         <div class="table-container">
             <el-table
                :data="tableData"  
                stripe
                style="width: 100%">
                <el-table-column
                label="问卷名称"
                :show-overflow-tooltip="true"
                width="200">
                    <template slot-scope="scope">
                        {{scope.row.formName}}
                    </template>
                </el-table-column>
                <el-table-column
                prop="classifyName"
                label="问卷类型">
                </el-table-column>
                <!-- <el-table-column
                prop="startTime"
                label="开始时间">
                <template slot-scope="scope">
                    {{scope.row.startTime | time}}
                </template>
                </el-table-column>
                <el-table-column
                prop="endTime"
                label="结束时间">
                 <template slot-scope="scope">
                    {{scope.row.endTime | time}}
                </template>
                </el-table-column> -->
                 <el-table-column
                prop="code"
                label="样本">
                <template slot-scope="scope">
                    {{`${scope.row.receiveNum}/${scope.row.sendNum}`}}
                </template>
                </el-table-column>
                <el-table-column
                label="操作"
                width="420">
                <template slot-scope="scope">
                    <div class="operate-button distance">
                        <SvgIcon icon-class="icondiaochafenxi"></SvgIcon>
                        <el-button
                        @click.native.prevent="continueCollectRow(scope.row.id)"
                        type="text"
                        size="small">
                        继续收集
                        </el-button>
                    </div>
                    <div class="operate-button distance">
                        <SvgIcon icon-class="iconfenxiwenjuan"></SvgIcon>
                        <el-button
                        @click.native.prevent="analyzeRow(scope.row.id, scope.row.formName)"
                        type="text"
                        size="small">
                        分析问卷
                        </el-button>
                    </div>
                    <div class="operate-button distance">
                        <SvgIcon icon-class="iconshanchu"></SvgIcon>
                        <el-button
                        @click.native.prevent="discardRow(scope.row.id)"
                        type="text"
                        size="small"
                        style="color: #ff5858">
                        废弃
                        </el-button>
                    </div> 
                    <div class="operate-button">
                        <SvgIcon icon-class="iconyulan"></SvgIcon>
                        <el-button
                        @click.native.prevent="previewRow(scope.row.id)"
                        type="text"
                        size="small">
                        预览
                        </el-button>
                    </div>
                    <div class="operate-button">
                        <SvgIcon icon-class="iconyulan"></SvgIcon>
                        <el-button
                        @click.native.prevent="seeRow(scope.row.id)"
                        type="text"
                        size="small">
                        查看原始问卷
                        </el-button>
                    </div>                    
                </template>
                </el-table-column>
            </el-table>
            <div class="center_pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    background
                    :current-page="page.page"
                    :page-sizes="[15,30,40,50]"
                    :page-size="page.pageSize"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="data.total">
                </el-pagination>
            </div>
        </div>
    </div>
 </div>
</template>

<script>
import {formatDate} from '@/utils/utils'

export default {
 name: 'ResearchResults',
 components: {},
 data() {
 return {
   pageTitle:['问卷调查', '已完成问卷'],
   page:{
        classifyId:'0',
        keyword:'',
        page:1,
        pageSize:15
     },
   typeOptions:[],
   data:{},
   tableData:[],
   api:{
        classify:'/form/rest/FormClassifyBusiness/listAll/v1',
        listEnd:'/form/rest/FormMainBaseBusiness/listEnd/v1',
        discard:'/form/rest/FormMainBaseBusiness/discard/v1',
        reStart:'/form/rest/FormMainBaseBusiness/reStart/v1'     
    }
 }
 },
 created(){
     this.classifyAll()
     this.initData()
 },
 mounted(){},
 methods:{
     classifyAll(){
       this.$post(this.api.classify).then(res=>{
           if(res.code ===200){
                this.typeOptions=res.data
                this.typeOptions.unshift( {classifyName:'全部', id:'0'})
           }
       })
     },
     initData(){
         this.$post(this.api.listEnd,this.page).then(res =>{
             if(res.code === 200){
                 this.data=res.data;
                 this.tableData=res.data.rows;                 
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: '获取列表失败'
                })
             }
         })
     },
      search(){
        this.resetTemp();
        this.initData() ;
     },
     continueCollectRow(id){
        this.$confirm('是否继续收集问卷?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
           this.$post(this.api.reStart, {formId:id})
            .then( res => {
                    if(res.code === 200){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: '操作成功'
                            
                            })
                        this.initData();
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.msg
                    })
                    }
                    
            })
        }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消'
            });
        });
     },
     analyzeRow(id, formName){
        this.$router.push({
             path:'/evaluationAndFeedback/analysisQuestionnaire',
             query:{
                 id:id,
                 title:formName
             }
         })
     },
     discardRow(id){
       this.$confirm('确定废弃该问卷?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
           this.$post(this.api.discard, {formId:id})
            .then( res => {
                    if(res.code === 200){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: '操作成功'
                            
                            })
                        this.initData() ;
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.msg
                    })
                    }
                    
            })
        }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消'
            });
        });
     },
     previewRow(id){
        this.$router.push({
             path:'/evaluationAndFeedback/questionnairePreview',
             query:{
                 id:id,
                 entryType:'new'
             }
         })
     },
     seeRow(id){
         this.$router.push({
             path:'/evaluationAndFeedback/originalQuestionnaireList',
             query:{
                 id:id
             }
         }) 
     },
     handleSizeChange(val){
        this.page.pageSize = val;
		this.initData()
     },
     handleCurrentChange(obj){
        this.page.page = obj;
        this.initData()
     },
      resetTemp(){
        this.page.page=1;
        this.page.pageSize=15;
     }
 },
 filters:{
     time(val){
         return val ? formatDate(val, 'yyyy-MM-dd') : ''     
     }
 }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss'
</style>



